.switch {
    width: 36px;
    height: 36px;
    display: inline-block;
    border-radius: 5px;
    margin: 0 2px 0 2px;
    -webkit-user-select: none;
    -moz-user-select: none;
     -ms-user-select: none;
         user-select: none;
    cursor: pointer;
    -webkit-transition: all .1s ease-out;
    -o-transition: all .1s ease-out;
    transition: all .1s ease-out;
}

.switch-hover {
    opacity: 0.8;
}

.switch-off {
    background-color: rgb(223, 223, 223);
}

.switch-on {
    background-color: rgb(77, 172, 230);
}


